

import Icon from './Icon';
import 'veui-theme-x/components/icon.less';
import Button from './Button';
import 'veui-theme-x/components/Button.js';
import 'veui-theme-x/components/button.less';
import Overlay from './Overlay';
import { dropdown, icons, overlay } from '../mixins';

export default {
  name: 'veui-dropdown',
  components: {
    'icon': Icon,
    'veui-button': Button,
    'veui-overlay': Overlay
  },
  mixins: [dropdown, icons, overlay],
  props: {
    ui: String,
    label: String,
    disabled: {
      type: Boolean,
      default: false
    },
    icon: String,
    options: Array
  },
  methods: {
    handleClick: function handleClick(option) {
      if (option.disabled) {
        return;
      }
      this.expanded = false;
      this.$emit('click', option.value);
    }
  }
};


